import React, { ReactNode, useEffect } from "react";

import Header from "./Header";
import styles from "@styles/Layout.module.scss";

type Props = {
	children: ReactNode
	categories: { category: string, count: number, href: string }[];
	tags: { tag: string, count: number, href: string }[];
	hideNavBtnIfSmallDevice?: boolean;
}

const Layout: React.FC<Props> = ({ children, categories, tags, hideNavBtnIfSmallDevice }) => {
	useEffect(() => {
		window.scrollTo(0, 0);
	}, []);

	return (
		<>
			<Header
				categories={ categories }
				tags={ tags }
				hideNavBtnIfSmallDevice={ hideNavBtnIfSmallDevice || false }
			/>
			<section className={ styles.content }>
				{children}
			</section>
		</>
	);
};

export default Layout;
